@import url(https://fonts.googleapis.com/css?family=Muli);
html,
body,
div,
h1, h2, h3, h4, h5, h6 {
  padding: 0;
  margin: 0; }

header {
  background-color: #F1F3F5;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: calc(100% - 48px);
  height: 40px;
  padding: 16px 24px;
  margin: 0; }
  header .logo {
    width: 40px;
    height: 40px; }
  header nav {
    display: flex;
    flex-direction: column;
    margin-left: 16px;
    margin-right: 16px;
    width: calc(100% - 32px); }
    header nav .organiser-name {
      height: 20px;
      color: #657885;
      font-family: "SF Pro Text";
      font-size: 14px;
      letter-spacing: -0.15px;
      line-height: 20px; }
    header nav .link {
      height: 20px;
      color: #00A5E6;
      font-family: "SF Pro Text";
      font-size: 14px;
      font-weight: 500;
      letter-spacing: -0.15px;
      line-height: 20px; }
  @media screen and (min-width: 728px) {
    header {
      width: calc(100% - 48px);
      height: 40px;
      padding: 16px 24px;
      flex-direction: row;
      position: relative; }
      header .link {
        position: absolute;
        float: right;
        right: 0; } }

.layout {
  display: flex;
  flex-direction: column;
  background-color: #F1F3F5;
  width: 100%;
  max-width: 320px;
  margin: 0;
  padding: 0;
  background-color: #ffffff; }
  .layout .layout-body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%; }
  @media screen and (min-width: 728px) {
    .layout {
      max-width: 728px;
      background-color: #F1F3F5; }
      .layout .layout-body {
        background-color: #FFFFFF;
        margin-bottom: 100px; } }

.container {
  padding: 14px 24px;
  margin: 0;
  max-width: 272px; }
  @media screen and (min-width: 728px) {
    .container {
      max-width: 728px; }
      .container.left {
        align-self: flex-start;
        margin-left: 70px; } }

footer {
  height: 80px;
  width: 320px;
  background-color: #FFFFFF;
  box-shadow: inset 0 1px 0 0 rgba(171, 185, 194, 0.32);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center; }
  footer > div {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%; }
  @media screen and (min-width: 728px) {
    footer {
      width: 100vw;
      position: absolute;
      bottom: 0;
      left: 0;
      overflow: hidden; }
      footer > div {
        width: 728px;
        justify-content: flex-end; } }

.input-container {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  width: 272px;
  flex: 1 1; }
  .input-container label {
    height: 20px;
    color: #25333D;
    font-family: "SF Pro Text";
    font-size: 14px;
    font-weight: bold;
    letter-spacing: -0.15px;
    line-height: 20px;
    margin: 4px 0;
    position: relative; }
    .input-container label span {
      position: absolute;
      float: right;
      right: 0;
      font-weight: normal;
      color: #879299; }
  .input-container .description {
    color: #879299;
    font-family: "SF Pro Text";
    font-size: 14px;
    margin: 4px 0; }
  .input-container input, .input-container select {
    height: 40px;
    max-width: 272px;
    border: 2px solid rgba(171, 185, 194, 0.32);
    border-radius: 4px;
    background-color: #FFFFFF;
    margin: 4px 0;
    padding: 0 12px; }
    @media screen and (min-width: 728px) {
      .input-container input, .input-container select {
        max-width: 540px; } }
  .input-container select option:disabled {
    color: #879299; }
  .input-container .select-range {
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: 0; }
    @media screen and (min-width: 728px) {
      .input-container .select-range {
        flex-direction: row;
        justify-content: flex-start;
        align-items: center; }
        .input-container .select-range select, .input-container .select-range input {
          width: 272px;
          margin-right: 24px; }
          .input-container .select-range select:last-child, .input-container .select-range input:last-child {
            margin-right: 0; } }
  .input-container .message {
    font-size: 12px;
    color: crimson; }
  @media screen and (min-width: 728px) {
    .input-container {
      width: 540px; }
      .input-container.m-2 {
        width: 270px;
        float: left; } }

button {
  border-radius: 4px;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: -0.32px;
  line-height: 24px;
  padding: 7px 0;
  margin: 4px;
  border: none; }
  button.primary {
    width: 176px;
    font-size: 16px; }
  button.blue {
    background-color: #00A5E6;
    color: #FFFFFF; }
  button.orange {
    background-color: #F45918;
    color: #FFFFFF; }
  button.secondary {
    width: 88px;
    border: 1px solid rgba(171, 185, 194, 0.32); }
  button.w200 {
    width: 200px !important; }

body {
  color: #25333D;
  background-color: #F1F3F5;
  font-size: 14px;
  font-family: 'Muli', sans-serif; }

#root {
  display: flex;
  justify-content: center;
  align-items: center; }
  @media screen and (min-width: 728px) {
    #root {
      position: relative; } }

a {
  text-decoration: none;
  color: #00A5E6; }
  a:hover {
    color: #F45918; }

h1, h2, h3, h4, h5, h6 {
  font-family: 'Muli', sans-serif;
  color: #25333D; }

h1 {
  font-size: 24px;
  font-weight: bold;
  letter-spacing: 0.33px;
  line-height: 29px;
  text-align: center;
  padding: 10px; }

